import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { Box, Button, FormControlLabel, styled, Switch, ToggleButtonGroup, Typography } from "@mui/material";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAngleUp, faAngleDown, faLock } from "@fortawesome/free-solid-svg-icons";

import { useTranslation } from "react-i18next";
import { colors } from "../../styles/colors";

import Modal from "../Modal";
import PlanSelection from "../Modal/PlanSelection";

import { RootState, AppDispatch } from "../../store";
import { downloadComic, fetchChapter, fetchComicInfo, pointToComic, setComicPurchase, setComicPurchaseInfo, setCurrentChapter, setEpisode, setIsDownload } from "../../store/comicSlice";
import { useDispatch, useSelector } from "react-redux";

import useUserInfo from "../../utils/checkUserInfoUtils";
import { ComicChapter, ComicInfoData, ComicPurchaseData, PurchaseInfo } from "../../type/comicType";

import DownloadModal from "../Modal/DownloadModal";
import { showCustomToast } from "../CustomToast";
import { ToggleButton } from "react-bootstrap";

import DownloadIcon from "@mui/icons-material/Download"; 
import LiveTv from "@mui/icons-material/LiveTv"; 
import { getFontClass } from "../../utils/helper";

// 定義資料項目的類型
interface VolumeItem {
  name: string;
  isLocked: boolean;
}

interface VolumeListProps {
  type: 'online' | 'download';
  color: string;
}

interface handlePlanSelectionOpenParam {
  chapter: number;
}

const VolumeList: React.FC<VolumeListProps> = ({ type, color }) => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const dispatch:AppDispatch = useDispatch();

  const userInfo = useSelector((state: RootState) => state.userInfo.userInfoData?.data);
  const videoInfo = useSelector((state: RootState) => state.video.videoInfoData);
  const comicInfo = useSelector((state: RootState) => state.comic.comicInfoData);
  const currentChapter = useSelector((state: RootState) => state.comic.currentChapter);
  const pageInfos = useSelector((state: RootState) => state.comic.pageInfoList);
  const isDownload = useSelector((state: RootState) => state.comic.isDownload);
  const language = useSelector((state: RootState) => state.init.language);
  
  const [data, setData] = useState<ComicChapter[] | null>(null);
  const uuid = comicInfo ? comicInfo?.comic_info.ci_uuid : '';
  const [isOpen, setIsOpen] = useState(false);
  const [modalContent, setModalContent] = useState('');
  const [isPlanSelectionOpen, setIsPlanSelectionOpen] = useState(false);
  const [isFilterOldToNew, setIsFilterOldToNew] = useState<boolean>(true); // 控制排序：預設為由舊至新

  const handleClose = () => setIsOpen(false);
  const handleConfirm = () => {
    navigate('/app/Recharge');
  };
  const handleCancel = () => {
    setIsOpen(false);
  };

  const handleButtons = [
    <Button className="button-full active mt-1 w-1/3" type="button" onClick={handleConfirm}>
      {t('確認')}
    </Button>,
    <Button className="button-full active mt-1 w-1/3" type="button" onClick={handleCancel}>
      {t('取消')}
    </Button>
  ];

  const handlePlanSelectionOpen = (params: handlePlanSelectionOpenParam) => {
    console.log("handlePlanSelectionOpen aaaaa");
    if (userInfo && Object.keys(userInfo).length > 0) {  
      if (comicInfo && comicInfo.comic_info && Object.keys(comicInfo.comic_info).length > 0) {
        if (comicInfo && params.chapter) {
          const chapter = comicInfo?.comic_chapters.filter((chapter) => chapter.ci_chapter_order === params.chapter.toString());
          const uuid = comicInfo?.comic_info.ci_uuid;
          if (chapter && uuid) {
            dispatch(pointToComic({
              uuid: uuid,
              chapter_uuid: chapter[0].ci_uuid
            }))
              .then((result) => {
                if (result.payload.result === 'ok') {
                  showCustomToast( result.payload.result_msg, 'error');
                } else if (!result.payload.error) {
                  dispatch(setComicPurchaseInfo(result.payload as PurchaseInfo));
                  setIsPlanSelectionOpen(true);
                }
              })
              .catch((error) => {
                console.error('Fetch failed:', error);
              });
          }
        } 
      }
    } else {
      navigate('/app/login');
    }
  }

  // 按下按鈕時，切換排序順序
  const handleSortToggle = () => {
    setIsFilterOldToNew(!isFilterOldToNew);
  };
  
  const handleEpisodes = (index: number, item: any) => {
    const chapter = comicInfo?.comic_chapters.filter((chapter) => chapter.ci_chapter_order === item.ci_chapter_order);
    const uuid = comicInfo?.comic_info.ci_uuid;
    dispatch(setCurrentChapter(Number(item.ci_chapter_order)));

    if (chapter && uuid && userInfo?.username) {
      if (isDownload) {
        const config = {
          uuid: uuid,
          chapter_uuid: chapter[0].ci_uuid,
        };
        dispatch(downloadComic(config))
          .then((result) => {
            if (result.payload.error) {
              if (
                userInfo.point >= Number(comicInfo.comic_info?.ci_point) ||
                Number(userInfo.ex_tickets.comic_download) > 0
              ) {
                if (chapter && uuid) {
                  dispatch(
                    pointToComic({
                      uuid: uuid,
                      chapter_uuid: chapter[0].ci_uuid,
                    })
                  )
                    .then((result) => {
                      if (result.payload.result === "ok") {
                        showCustomToast(result.payload.result_msg, "error");
                      } else if (!result.payload.error) {
                        dispatch(
                          setComicPurchaseInfo(result.payload as PurchaseInfo)
                        );
                        setIsPlanSelectionOpen(true);
                      }
                    })
                    .catch((error) => {
                      console.error("Fetch failed:", error);
                    });
                }
              } else {
                setModalContent(t("點數、下載券不足，是否前往加值?"));
                setIsOpen(true);
              }
            } else {
              setDownloadOpen(true);
            }
          })
          .catch((error) => {
            console.log("downloadComic exception: ", error);
          });
      } else {
        dispatch(fetchChapter({
          uuid: uuid,
          cat: 1,
          chapter_uuid: chapter[0].ci_uuid,
          view_type: 0,
        }))
          .then((result) => {
            if (result.payload.error) {
              handlePlanSelectionOpen({ chapter: Number(item.ci_chapter_order)});
            } else if (result.payload.infoList){
              navigate(`/app/comicPage/${Number(item.ci_chapter_order)}`);
              dispatch(setCurrentChapter(Number(item.ci_chapter_order)));
            }
          })
          .catch((error) => {
            console.error('Fetch failed:', error);
          });
      }
    } else {
      navigate('/app/login');
    }
  };

  const [downloadOpen, setDownloadOpen] = useState(false);
  const handleDownloadClose = () => setDownloadOpen(false);

  // 根據排序狀態調整話數的顯示順序
  
  const sortedData = data
  ? (isFilterOldToNew ? data : [...data].reverse())
  : [];
  
  useEffect(() => {
    if (comicInfo) {
      setData([...comicInfo?.comic_chapters]);
    }
  }, [comicInfo]);

  useEffect(() => {
    return () => {
      dispatch(setIsDownload(false));
    }
  }, []);

  const handleToggle = (event: React.ChangeEvent<HTMLInputElement>) => {
    dispatch(setIsDownload(event.target.checked));
  };

  const MaterialUISwitch = styled(Switch)(({ theme }) => ({
    width: 62,
    height: 34,
    padding: 7,
    '& .MuiSwitch-switchBase': {
      margin: 1,
      padding: 0,
      transform: 'translateX(6px)',
      '&.Mui-checked': {
        color: '#fff',
        transform: 'translateX(22px)',
        '& + .MuiSwitch-track': {
          opacity: 1,
          backgroundColor: '#aab4be',
        },
      },
    },
    '& .MuiSwitch-thumb': {
      backgroundColor: '#001e3c',
      width: 32,
      height: 32,
      '&::before': {
        content: "''",
        position: 'absolute',
        width: '100%',
        height: '100%',
        left: 0,
        top: 0,
      },
    },
    '& .MuiSwitch-track': {
      opacity: 1,
      backgroundColor: '#aab4be',
      borderRadius: 20 / 2,
    },
  }));

  return (
    <Box className={getFontClass(`${language}_m`)} sx={{ padding: "10px 1rem" }}>
      {/* 排序選項 */}
      <Box sx={{ marginTop: "0px", display: "flex", alignItems: "center" }}>
        <div className="flex justify-between w-full">
          <div className="flex items-center">
            <FormControlLabel
              control={
                <MaterialUISwitch 
                  sx={{ m: 1 }}
                  checked={isDownload}
                  onChange={handleToggle}
                  checkedIcon={
                    <div
                      style={{
                        width: "32px",
                        height: "32px",
                        display: "flex",
                        alignItems: "center",
                        justifyContent: "center",
                        backgroundColor: '#984B4B',
                        borderRadius: '50%'
                      }}
                    >
                      <DownloadIcon style={{ color: "white", fontSize: "20px" }} />
                    </div>
                  }
                  icon={
                    <div
                      style={{
                        width: "32px",
                        height: "32px",
                        display: "flex",
                        alignItems: "center",
                        justifyContent: "center",
                        backgroundColor: '#984B4B',
                        borderRadius: '50%'
                      }}
                    >
                      <LiveTv style={{ color: "white", fontSize: "20px" }} />
                    </div>
                  } 
                />
              }
              label={ isDownload ? t("下載") : t("線上") }
            />
          </div>

          <div className="flex items-center">
            <Typography sx={{ color: "#fff", marginRight: "8px" }}>
              {t("排序：")}
            </Typography>
            <Button sx={{ color: "#fff" }} onClick={handleSortToggle}>
              {isFilterOldToNew ? t("由新至舊") : t("由舊至新")}
              <FontAwesomeIcon
                icon={isFilterOldToNew ? faAngleUp : faAngleDown}
                style={{ marginLeft: "8px" }}
              />
            </Button>
          </div>
        </div>
      </Box>

      {/* 顯示話數按鈕區塊 */}
      <Box
        sx={{
          display: "grid",
          gridTemplateColumns: "repeat(2, 1fr)", // 兩列佈局
          gap: "8px",
          marginTop: "5px",
        }}
      >
        {sortedData?.map((item, index) => (
          <Button
            className="button-shorted bg-background justify-between flex items-center"
            style={ 
              Number(item.ci_chapter_order) === currentChapter 
              ? {backgroundColor: '#878787',color: '#D3D3D3'}
              : {backgroundColor: color, color: colors.primary.inactive}
            }
            key={index}
            onClick={() => handleEpisodes(index, item)}
          >
            <span className="flex-1 text-left">{item.ci_name}</span>
            {!item.has_permission && <FontAwesomeIcon icon={faLock} className="text-gray-200"/>}
          </Button>
        ))}
      </Box>
      {
        ((videoInfo && videoInfo.data.point) || (comicInfo && comicInfo.comic_info.ci_point)) &&
        (
          <PlanSelection
            open={isPlanSelectionOpen}
            setIsPlanSelectionOpen={setIsPlanSelectionOpen}
            costPoint={Number(comicInfo!.comic_info.ci_point)}
            type= {isDownload ? `comicDownloadTicket` : `comicOnlineTicket`}
          />
        )
      }
      <Modal
        open={isOpen}
        onClose={handleClose}
        content={[modalContent]}
        buttons={handleButtons}
      />
      <DownloadModal
        open={downloadOpen}
        onClose={handleDownloadClose}
        title={t('請選擇您要下載的檔案格式')}
        type = 'comic'
      />
    </Box>
  );
};

export default VolumeList;
