import React, { useState, useMemo, useEffect, createContext } from "react";
import BackBar from "../../components/BackBar";
import { useTranslation } from "react-i18next";
import BasicTabs from "../../components/BasicTabs";
import Button from "../../components/Button";
import ComicMap from "../../components/Comic/ComicMap";
import VipButton from "../../components/AppBar/VipButton";
import { useLocation, useNavigate } from "react-router-dom";
import Description from "../../components/Description";
import VolumeList from "../../components/VolumeList";
import PlanSelection from "../../components/Modal/PlanSelection";
import Modal from "../../components/Modal";

import { useDispatch, useSelector } from "react-redux";
import { AppDispatch, RootState } from "../../store";
import { fetchComicInfo } from "../../store/comicSlice";

import useUserInfo from "../../utils/checkUserInfoUtils";

import { colors } from "../../styles/colors";
import ComicToolbar from "../../components/Comic/ComicToolbar";
import Loading from "../../components/Loading";
import { getFontClass, switchName } from "../../utils/helper";

interface TabContent {
  key: string;
  label: string;
  content: JSX.Element | null;
}

const Comic: React.FC = () => {
  const location = useLocation();
  const { uuid } = location.state || {};
  const dispatch: AppDispatch = useDispatch();

  useEffect(() => {
    console.log(uuid);
    dispatch(fetchComicInfo(uuid));
  }, [dispatch, uuid]);

  const loading = useSelector((state: RootState) => state.comic.loading);
  const comicInfo = useSelector(
    (state: RootState) => state.comic.comicInfoData
  );
  const comicList = useSelector(
    (state: RootState) => state.comic.comicListData
  );
  const language = useSelector(
    (state: RootState) => state.init.language
  );


  const { t } = useTranslation();
  const navigate = useNavigate();
  const [activeTab, setActiveTab] = useState(0);

  const accountInfo = useSelector(
    (state: RootState) => state.userInfo.userInfoData
  );
  const [isVip, setVip] = useState(false);

  const [isOpen, setIsOpen] = useState(false);
  const [modalContent, setModalContent] = useState("");
  const [isPlanSelectionOpen, setIsPlanSelectionOpen] = useState(false);
  const [tabIndex, setTabIndex] = useState(0);

  const handleClose = () => setIsOpen(false);

  const scrollToTop = () => {
    window.scrollTo({ top: 0, behavior: "auto" });
  };

  const handleReading = () => {
    if (accountInfo && accountInfo.data && typeof accountInfo.data === "object") {
      navigate("/app/ComicPage/1");
    } else {
      navigate("/app/login");
    }
  };
  const handle2GetMorePage = () => {
    navigate(`/app?`, { state: { categoryName: "漫畫" } });
  };

  useEffect(() => {
    const currentUnixTime = Math.floor(Date.now() / 1000);
    if (
      Number(accountInfo?.data.category_exp) > currentUnixTime ||
      Number(accountInfo?.data.category2_exp) > currentUnixTime
    ) {
      setVip(true);
    } else {
      setVip(false);
    }
  });

  const tabContent: TabContent[] = useMemo(
    () => [
      {
        key: "漫畫介紹",
        label: t("漫畫介紹"),
        content: comicInfo ? <Description type="comic" setTabIndex={setTabIndex}/> : null,
      },
      {
        key: "目錄",
        label: t("目錄"),
        content:
          comicInfo && comicInfo ? (
            <VolumeList type="online" color={colors.background}/>
          ) : null,
      },
    ],
    [uuid, comicInfo]
  );

  return (
    <div className={getFontClass(`${language}_m`)}>
      {loading && uuid ? (
        <div>
          <Loading />
        </div>
      ) : (
        <>
          <BackBar home />
          {/* 漫畫縮圖 */}
          <div className="relative aspect-[0.71]">
            <img
              loading="lazy"
              src={`https:${comicInfo?.comic_info?.pic_big}`}
              alt="Comic thumbnail"
              className="object-cover w-full h-full"
              onError={(e) => {
                (e.target as HTMLImageElement).onerror = null; // 防止無限觸發
                (
                  e.target as HTMLImageElement
                ).src = require("../../images/qc-default-img.jpg"); // 使用本地圖片
              }}
            />
            <div className="absolute bottom-0 left-0 right-0 px-4 py-2 bg-backgroundTrans text-white">
              <p className="text-xl font-bold">
                {switchName(comicInfo?.comic_info, language)}
              </p>
            </div>
          </div>

          {/* 開始閱讀按鈕 */}
          <Button
            className="button-full-flat active py-2"
            onClick={handleReading}
          >
            <span className="text-xl font-bold">{t("開始閱讀")}</span>
          </Button>

          {/* 訂閱資訊和VIP按鈕 */}
          {!isVip && (
            <div
              className="p-4 bg-focusText text-white flex items-center justify-between"
              style={{
                background: "linear-gradient(45deg, #49265b, #212c49, #000000)",
              }}
            >
              <span className="text-base">
                {t("CNY¥")}
                <span className="text-lg font-bold">97</span>
                {t("/月 全館暢看!!!")}
              </span>
              <VipButton />
            </div>
          )}

          {/* 分頁內容 */}
          <BasicTabs tabs={tabContent} initialValue={tabIndex} setTabIndex={setTabIndex}/>

          {/* 漫畫地圖 */}
          {activeTab !== 1 && (
            <>
              <ComicToolbar
                title={t("更多漫畫")}
                loadMore={handle2GetMorePage}
              />
              <ComicMap data={comicList?.products} onCardClick={scrollToTop} />
            </>
          )}
          <Modal open={isOpen} onClose={handleClose} content={[modalContent]} />
          {comicInfo && comicInfo.comic_info && (
            <PlanSelection
              open={isPlanSelectionOpen}
              setIsPlanSelectionOpen={setIsPlanSelectionOpen}
              costPoint={comicInfo.comic_info.ci_point}
            />
          )}
        </>
      )}
    </div>
  );
};
export default Comic;
